<template>
    <div class="layout">
        <Layout>
            <Header class="header">
                <div class="layout-logo"></div>
                <div class="layout-nav">

                </div>

            </Header>
            <Layout>
                <Sider hide-trigger :style="{background: '#fff'}">
                    <Menu
                        v-if="haveLists"
                        accordion
                        :active-name="haveLists ? articleLists[0].children[0].value:''"
                        theme="light" width="auto"
                        :open-names="[haveLists ? articleLists[0].value : '']"
                        @on-select="handleSideSelect"
                        >

                        <Submenu v-for=" list in articleLists" :key="list.value" :name="list.value">
                            <template slot="title">
                                {{list.label}}
                            </template>
                            <MenuItem v-for="(article, index) in list.children" :key="article.value" :name="article.value">{{(index+1)+"."+article.label}}</MenuItem>
                        </Submenu>
                    </Menu>
                </Sider>
                <Layout :style="{padding: '0 24px 24px'}">
                    <Content :style="{ minHeight: '280px', background: '#fff',margin: '24px 0'}">
                        <mavon-editor :value="contents" :subfield="false" :toolbarsFlag="false" defaultOpen="preview" :ishljs="true"/>
                    </Content>
                </Layout>
            </Layout>
        </Layout>
    </div>
</template>

<script>
import {Layout,Header,Menu,MenuItem,Submenu,Content,Sider} from "iview"
import {getArticle,getArticleLists} from "../../api"
export default {
    data() {
        return {
            contents:"",
            articleLists:[],
        }
    },
    computed: {
        haveLists() {
            console.log(this.articleLists.length)
            return this.articleLists.length?true:false
        }
    },
    methods: {
        async getArticle(name) {
            let data = await getArticle(name);
            this.contents = data.data;
        },
        async getArticleLists() {
            let data = await getArticleLists();
            this.articleLists = data.data;
            this.initArticle(data.data[0].children[0].value)
        },
        initArticle(name) {
            this.getArticle(name)
        },
        handleSideSelect(name) {
            this.getArticle(name)
        }

    },
    mounted() {
        this.getArticleLists();
    },
    components: {
        Layout,
        Header,
        Menu,
        MenuItem,
        Submenu,
        Content,
        Sider,
    }
}
</script>

<style lang="less" scoped>
.layout{
    height: 100%;
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    .header {
        background: #fff;
        padding:0;
        border-bottom: 1px solid #CCC;
    }
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #F0F0F0;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 70px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
}
</style>



