import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/view/home/home.vue'
import Contents from "@/view/contents/contents.vue"
import Article from "@/view/contents/article/article.vue"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/content',
      name: 'Contents',
      component: Contents,
      children: [
          {
              path: "/content/article",
              component: Article
          }
      ]
    }
  ]
})



