<template>
	<div class="home">
		<div class="content">
			<h1></h1>
			<div class="btn" @click="handleClick">
				<span class="btn-content"></span>
			</div>
		</div>
		<div class="sky-bg">
			<sky-background round-color="rgba(255,255,255,0.6)"></sky-background>
		</div>
	</div>
</template>
<script >
	import skyBackground from "../../components/sky.vue"
	export default {
		name : "home",
		data() {
			return {
				articleLists:[],
			}
		},
		methods: {
			handleClick() {
				this.$router.push("/content/article")
			}
		},
		components: {
			skyBackground
		}
	}
</script>
<style lang="less" scoped>
	.home {
		position: relative;
		width: 100%;
		height: 100%;
		background: linear-gradient(#7bc4e7,#F0F0F0);
		.content {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom:0;
			margin: auto;
			border-radius: 10px;
			z-index: 10;
			width: 460px;
			height: 200px;
			background: rgba(0,0,0,0.1);
			text-align: center;
			h1 {
				color:#F0F0F0;
				margin: 40px 0 20px 0;
			}
			.btn {
				display: inline-block;
				height: 40px;
				line-height: 40px;
				padding: 0 20px;
				border: 1px solid #F0F0F0;
				border-radius: 4px;
				color: #F0F0F0;
				text-align: center;
				cursor: pointer;
				transition: all .2s;
				&:hover {
					border-color: rgb(18, 166, 230);
					background:rgba(255,255,255,0.6);
					color: rgb(18, 166, 230);
				}
				&:active {
					border-color: rgb(18, 106, 230)
				}
			}
		}
		.sky-bg {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
</style>



